import React from 'react'
import { Label, Input, FormGroup } from 'reactstrap'
import themeConfig from '../../configs/themeConfig'

const InputTypeNum = ({
  error,
  value,
  handleValue,
  placeholder,
  label,
  name,
  handleValueBlur = () => {}
}) => {
  return (
    <FormGroup className="form-label-group mt-2 mb-0">
      <Input
        className="input-label"
        type="type"
        maxLength={10}
        value={value}
        name={name}
        onChange={handleValue}
        onBlur={handleValueBlur}
        onKeyPress={(e) => handleKeyMobileNumber(e)}
        id="data-price1"
        placeholder={placeholder}
        style={{ borderColor: error ? 'red' : '' }}
      />
      <Label
        className={themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'}
        for="data-price1"
      >
        {label}
      </Label>
      {error && (
        <span style={{ color: 'red', fontSize: '0.8rem' }}>{error}</span>
      )}
    </FormGroup>
  )
}
function handleKeyMobileNumber(e) {
  let key = e?.keyCode || e?.which
  key = String.fromCharCode(key)
  const regex = /^[0-9]*$/
  if (!regex.test(key)) {
    e.preventDefault()
    return false
  }
}
export default handleKeyMobileNumber
